/* RollingBanner λ¨λ“ μ»΄ν¬λ„νΈμ© CSS - rollingBanner.css */


/* λ΅¤λ§λ°°λ„ κ²‰λ°•μ¤ μ„Έν… */
.rolling_container{
    margin-top: 20vh;
    display: flex;
    flex-direction: column;
    justify-content: center;
    align-items: center;

    /* λ°°λ„ μ‚¬μ΄μ¦ μ¤„μ΄κΈ° */
    /* transform: scale(0.8); */
}

/* λΌμΈ svg κ²‰λ°•μ¤ */
.rolling_container .line_bx{
    display: flex;
    justify-content: center;
    align-items: center;
}
/* ν™”μ‚΄ν‘ κΌ¬λ¦¬ svg κ³µν†µ μ„Έν… */
.rolling_container .line_bx .arrow-head{
    width: 20px;
    height: 20px;
}
.rolling_container .line_bx .arrow-tail{
    width: 20px;
    height: 20px;
}

/* λΌμΈ μ¤‘ κ°€μ΄λ° μ‹¤μ„  svg μ„¤μ • */
.rolling_container .line_bx .border{
    /* calc(λ°°λ„κ°€λ΅κ°’700px - ((λ¨Έλ¦¬/κΌ¬λ¦¬20px - μ‚¬μ΄κ°„κ²©10px - μ 3px - μ‚¬μ΄κ°„κ²©10px)*2) */
    width: calc(700px - 86px);
    height: 20px;
    margin: 0 10px;
}
/* ν™”μ‚΄ν‘ κΌ¬λ¦¬μ™€ κ°€μ΄λ° μ‹¤μ„  μ‚¬μ΄ μ›μ„ μ„ν• λ°•μ¤ */
.rolling_container .line_bx .cir{
    display: flex;
    align-items: center;
}
.rolling_container .line_bx .cir::before{
    content: '';
    display: block;
    width: 3px;
    height: 3px;
    border-radius: 50%;
    background-color: var(--arrow-grey-color);
    margin-left: 10px;
}
.rolling_container .line_bx .cir::after{
    content: '';
    display: block;
    width: 3px;
    height: 3px;
    border-radius: 50%;
    background-color: var(--arrow-grey-color);
    margin-right: 10px;
}

/* λΌμΈ μ¤‘ κΌ¬λ¦¬ λ¨μ–‘(.right) μ„¤μ • */
.rolling_container .line_bx svg:nth-child(3){
    transform: rotateY(180deg)
}

/* λ°°λ„ λ·°ν¬νΈ : λ°°λ„ ν‘μ‹ μμ—­ */
.rolling_banner>.wrap{
    position: relative;
    /* λ¶€λ¨μκ²© - .wrapμ κ°€μƒμ”μ† */


    width: 700px;
    height: 200px;
    
    overflow: hidden;
    /* μμ‹μ”μ† μ¨κΈ°κΈ° */

    /* outline: 1px solid red; */
}

/* μΆ/μ° νμ΄λ“μΈμ•„μ›ƒ μμ—­ λ§λ“¤κΈ° */
.rolling_container .wrap::before{
    content: '';
    position: absolute;
    top: 0;
    left: 0;
    width: 100px;
    height: 100%;
    z-index: 1;
    background: linear-gradient(to right, #faf8f5 0%, #faf8f5bf 51%, #faf8f509 100%);
}
.rolling_container .wrap::after{
    content:'';
    position: absolute;
    top: 0;
    right: 0;
    width: 100px;
    height: 100%;
    z-index: 1;
    background: linear-gradient(to left, #faf8f5 0%, #faf8f5bf 51%, #faf8f509 100%);
}


/* λ°°λ„ λ‚΄μ© wrapper */
.rolling_container .roller{
    position: absolute;
    /* λ³µμ‚¬ν• λ°°λ„ λ°μ΄ν„° λ¦¬μ¤νΈ λ’¤μ— μ¤κ² λ§λ“¤κΈ° */
}

/* λ°°λ„ λ°μ΄ν„° λ¦¬μ¤νΈ (ul) */
.rolling_container .roller>ul{
    width: 100%;
    margin: 0;
    padding: 10px 0;

    display: flex;
    flex-flow: row nowrap;
    align-items: flex-end;
    /* gap: 10px; */
}

/* κ° μ¬λΌμ΄λ“ (li) */
.rolling_container ul>.slide{
    display: flex;
    flex-direction: column;
    align-items: center;
    gap: 10px;
    width: 150px;

    /* outline: 1px dashed blue; */
}

/* μ΄λ―Έμ§€ μ”μ† κ³µν†µ μ„Έν… */
.rolling_container li>img{
    object-fit: cover;
}
/* ν…μ¤νΈ μ”μ† κ³µν†µ μ„Έν… */
.rolling_container li>.rolTxt{
    display: block;
    font-size: 15px;

    /* outline: 1px dashed black; */
}




/* ν•λ‹¨ μμ—­ μ„μ½μ κ²½κ³„μ„  svg μ”μ†λ“¤ μ„Έν… */
.Group-1{
    stroke:none;
    stroke-width: 1;
    fill: none;
    fill-rule: evenodd;
}
.Group-2{
    stroke-linecap: round;
    stroke:var(--arrow-grey-color);
}
.Group-3{
    stroke: none;
    stroke-width: 1;
    fill: none;
    fill-rule: evenodd;
}

.Line-1{
    stroke-linecap: square;
    stroke: var(--arrow-grey-color);
}
.Line-2{
    stroke: var(--arrow-grey-color);
}


