/* 500px μ‚¬μ΄μ¦ μ΄ν• λ―Έλ””μ–΄μΏΌλ¦¬ μ‹μ‘ */
@media screen and (max-width: 500px) {
    .rolling_container{
        transform: scale(0.5);
    }
    /* λ΅¤λ§ λ°°λ„ κ° μ‚¬μ΄λ“ νλ¦¬κ² ν•λ” κΈ°λ¥ - κ°€μƒμ”μ† */
    .rolling_container .line_bx .cir::before{
        display: none;
    }
    .rolling_container .line_bx .cir::after{
        display: none;
    }
}
