@charset "utf-8";
/* κ° μ ν’ μ†κ° λ¨λ“ CSS - details.css */


/* μ μΌ κ²‰ λ°•μ¤ */
.details_container{
    position: fixed;
    top: 0;
    left: 0;
    right: 0;
    bottom: 0;
    z-index: 3;
    overflow-y: auto;

    /* μ²μμ— μ¨κ²¨λ‘κΈ° */
    display: none;
}


#details{
    position: absolute;
    top: 0;
    left: 0;
    right: 0;

    background-color: var(--btn-lemon-color);
}
.details{
    display: flex;
    flex-direction: column;
    justify-content: center;
    align-items: center;
    
    margin-bottom: 100px;
}


/* λ‹«κΈ° λ²„νΌ κ²‰λ°•μ¤ */
.details .cls_btn_container{
    max-width: 1400px;
    height: 80px;
    margin: 20px auto 0;
    padding: 0 30px;

    position: fixed;
    top: 0;
    left: 0;
    right: 0;
    
    display: flex;
    justify-content: space-between;
    align-items: center;
    flex-direction: row-reverse;

    -webkit-user-select: none;
    -moz-user-select: none;
    -ms-user-select: none;
    user-select: none;
    
    /* border: 1px solid #000; */
}
/* λ‹«κΈ° λ²„νΌ */
.details .cls_btn_container .close{
    display: block;
    padding: 10px;

    cursor: pointer;

    /* border: 1px solid red; */
}


/* μ ν’λ… λ°•μ¤ μ„Έν… */
.details .product_name_container{
    padding: 100px 20px 0;
    margin-bottom: 50px;
}
/* μ ν’λ… ν°νΈκ΄€λ ¨ μ„Έν… */
.details .product_name_container .name{
    font-family: 'Cormorant Garamond', serif;
    font-size: 7vmax;
    color: var(--ft-navy-color);
}
/* μ ν’λ…(λ‹¨μ–΄) μ„μΉ μ„Έν… */
.details .product_name_container .name>span{
    display: flex;
    gap: 20px;
}
/* μ ν’λ…(λ’¤μ— μ¤λ” λ‹¨μ–΄) κ°λ³„ μ„Έν… */
.details .product_name_container .name .letter:nth-child(2){
    font-style: italic;
    color: var(--ft-lemon-color);
}
.details .product_name_container .name .letter.rouge:nth-child(2){
    color: var(--ft-rose-color);
}

/* κ·Έλ¦¬λ“ λ°•μ¤ μ„Έν… */
.details .gridBx{
    max-width: 1400px;
    display: grid;
    /* κ°€λ΅ κ·Έλ¦¬λ“ ν…ν”λ¦Ώ */
    grid-template-columns: repeat(12, 1fr);
    grid-column-gap: 20px;

    padding: 20px 20px 0;
    margin-bottom: 50px;

    outline: 1px dashed black;
}
.details .product_img_container{
    grid-column: 1/7;
}
.details .product_desc_container{
    grid-column: 7/13;
}



/* μ ν’ μ λ¦¬λ³‘ img μ”μ† μ„Έν… */
.details .product_img_container img#product{
    width: 100%;
    margin-top: 30vh;

    outline: 1px dashed red;
}

