.swipe_tit_container{

}
/* νƒ€μ΄ν‹€ κ²‰λ°•μ¤ */
.swipe_tit_container .wrap:has(.title){
    max-width: 1400px;
    padding: 50px 20px 0px;
    margin: 50px auto 20px;

    /* border: 1px solid #000; */
}
/* νƒ€μ΄ν‹€(h2)μ”μ† */
.swipe_tit_container .title{
    position: relative;
    z-index: 1;

    font-family: 'Cormorant Garamond', serif;
    font-size: 6vmax;
    color: var(--ft-navy-color);

    /* border: 1px solid red; */
}

.swipe_deco_container{
    position: relative;
}
.swipe_deco_container .wrap:has(.deco_img){
    position: absolute;
    top: -400px;
    right: -5%;
    transform: rotate(-15deg);
}
.swipe_deco_container .deco_img{
    width: 80%;
}

/* μ¤μ™€μ΄νΌ λ°°λ„ κ²‰λ°•μ¤ */
section.swipe_banner {
    width: 100%;
    height: 100%;
    padding: 100px 0 0;
    margin-bottom: 50px;
}
/* μ¤μ™€μ΄νΌ */
.swiper-product {
    width: 100%;
    height: 100%;

    /* κΈνλ”κ±° λ§‰κΈ° */
    -webkit-user-select: none;
    -moz-user-select: none;
    -ms-user-select: none;
    user-select: none;
}

/* κ° μ¬λΌμ΄λ“ (li) */
.swiper-slide.gin {
    position: relative;
    /* λ¶€λ¨μκ²© - νƒ€μ΄ν‹€μ κ²‰λ°•μ¤ */
    display: flex;
    flex-direction: column;
    justify-content: center;
    align-items: center;

    background: transparent;
    text-align: center;

    cursor: pointer;

    /* aspect-ratio: 583/1440; */

    /* border: 3px solid hotpink; */
}


/* μ ν’ μ΄λ―Έμ§€ */
.swiper-slide.gin img {
    margin: 0 auto;
    display: block;
    width: 75%;
    height: 75%;
    object-fit: cover;

    /* border: 2px dashed red; */
}
/* νƒ€μ΄ν‹€ κ²‰λ°•μ¤ */
.swiper-slide.gin .wrap:has(.product_tit.gin){
    position: absolute;
    /* λ¶€λ¨ - κ° li */
    top: 50%;
    left: 50%;
    transform: translate(-50%, -50%);
    height: 0;
    opacity: 1;

    overflow: hidden;
}

/* μ ν’ νƒ€μ΄ν‹€ */
.swiper-slide.gin .product_tit.gin{
    /* position: absolute; */

    font-family: 'Cormorant Garamond', serif;
    font-size: 7vmax;
    line-height: 1;
    white-space: nowrap;
    font-weight: bold;
    color: var(--ft-navy-color);
    text-shadow: 1px 1px 1px #000;

    /* outline: 1px solid red; */
}





