    }
    .product_desc_container{
        grid-column: 1/13;
        order: 2;
    }
    .product_img_container .wrap:has(img#product){
        text-align: center;
    }
    .product_img_container img#product{
        margin-top: 0;
        width: 80%;
    }
    /* λ°μ½” μ΄λ―Έμ§€ μ„Έν… */
    /* original μ ν’ */
    .wrap:has(.citrus){
        width: 250px;
        height: 250px;
        top: 32%;
