@charset "utf-8";

/* μ¤ν¬λ΅¤ μ λ„ ν‘μ‹ μ»΄ν¬λ„νΈ CSS - scroll.css */

.scrl_container{
    display: flex;
    flex-direction: column;
    align-items: center;
    gap: 10px;

    position: absolute;
    top: 20%;
    right: 5%;

    /* border: 2px dashed red; */

    /* animation: scrlAni 1s ease-in-out infinite; */
}
/* svg μ”μ† μ„Έν… */
.scrl_container>svg{
    width: 20px;
    height: 20px;
}
.scrl_container svg.arrow-tail{
    transform: rotate(90deg);
    /* animation: tailAni 1s ease-out infinite; */
}
.scrl_container svg.arrow-head{
    transform: rotate(-90deg);
    /* animation: headAni 1s ease-out infinite; */
}
/* μ  μ„Έν… */
.scrl_container .dot{
    width: 3px;
    height: 3px;
    background-color: var(--arrow-grey-color);
    border-radius: 50%;
}
/* κΈ€μ μ„Έν… */
.scrl_container .txt{
    font-family: 'Asap', sans-serif;
    writing-mode: vertical-rl;
    font-size: var(--font-medium);
    color: var(--ft-navy-color);
    font-weight: 500;
}

/* μ• λ‹λ©”μ΄μ… ν‚¤ν”„λ μ„ */
@keyframes tailAni {
    0%{
        transform: rotate(90deg) translateX(-10px);
    }
    100%{
        transform: rotate(90deg) translateX(0px);
    }
}
@keyframes headAni {
    0%{
        transform: rotate(-90deg) translateX(10px);
    }
    100%{
        transform: rotate(-90deg) translateX(0px);
    }
}
